body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body-container{background-color:white}#nav-up{background-color:#0D98BA;color:#000C66;opacity:0.3;position:fixed;bottom:4%;right:3%;z-index:4000;border-radius:12px;transition:all 0.2s ease-in-out}#nav-up>.up-ic{cursor:pointer;border-bottom:1px solid #0D98BA}#nav-up:hover,#nav-up:focus{opacity:0.6;background-color:#000C66;color:#0D98BA}#nav-up>p{font-size:30px;margin:1px auto;text-align:center;--badge-color: #ffffff98;--badge-bg-color: #9b6ab84e}#nav-up>p:hover{--badge-color: #0D98BA;--badge-bg-color: #000c66d3}.with-icon{margin-left:8px !important;cursor:context-menu;width:100%;border-bottom:none}.badge-nav-up{position:relative;bottom:6px;right:10px;font-size:13px;color:var(--badge-color);background-color:var(--badge-bg-color)}

:root{
    --bottom-bord: 1px solid #8a77cf0a;
    --nav-bg: #9e9e9363;
    --nav-color:#000C66;
    --hover-nav-color: #0D98BA;
    --nav-item-font: 11px;
}
/* FIRST HEADER NAV */
.nav-bg{
    font-family: 'Coda Caption', sans-serif;
    height: 90px;
}
#no-bottom-bord{
    border-bottom: none;
    transition: all 0.5s ease-in-out;
}
#bottom-bord{
    padding: 1rem 1rem 0 1rem;
    height: 60px;
    border-bottom: 1px solid #8a77cf0a;
    border-bottom: var(--bottom-bord);
    background-color: #9e9e9363;
    background-color: var(--nav-bg);
    --nav-item-font: 10px;
    --nav-color:#c1bcd4;
    transition: all 0.5s ease-in-out;
}
.navbar-toggler-icon{
    background-color: #000C66;
    background-color: var(--nav-color);
    outline: none;
}
.navbar-toggler:focus{
    box-shadow: none;
    border: none;
    outline: none;
}
.header-nav-item{
    text-decoration: none;
    color: #000C66;
    color: var(--nav-color);
    margin: auto 0.5rem;
    font-size: 11px;
    font-size: var(--nav-item-font);
    display: flex;
}
.header-nav-item:hover{
    text-decoration: none;
    color: #0D98BA;
    color: var(--hover-nav-color);
}
.nav-brand-custom{
    display: flex;
    font-size: 12px;
}
.icon-only{
    --badge-color: #0D98BA;
    --badge-bg-color: #000c66d3;
}
.icon-only:hover{
    --badge-color: #3ac0e2;
}
.badge{
    position: relative;
    bottom: 6px;
    right: 8px;
    font-size: 13px;
    color: var(--badge-color);
    background-color: var(--badge-bg-color)
}
@media(max-width:576px){
    .navbar-collapse{
        background-color: #ffffff !important;
        margin-top: 5px;
    }
    .fav::after{
    content: "Favorite Items";
    }
}
@media(min-width:576px){
    #prod-select-info{
        display: flex;
        height: 36px;
        border-radius: 20px;
        box-shadow: 0px 1px 7px 0px #333322
    }
}
/* FIRST HEADER NAV */
/* MAIN HOME PAGE */
.home-body{
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
    padding-top: 4.2rem;
    max-width: 100%;
    margin: 0;
}
#large-home-banner{
    align-items: center;
    height: 400px;
    background-image: 
    linear-gradient(180deg,#000c66cb,#0c19799f,#1f057476),   
    url(https://i.picsum.photos/id/418/1349/500.jpg?hmac=99aEMWU5Z_whQvGG1mVYvvsD4wtg65ejlb350PtzQx0);
    background-repeat: no-repeat;
    background-size: cover;
}
#search-categories{
    border-top-left-radius: 25px;
    width: 40%;
    outline: none;
    /* color: #ffffff; */
    background-color: #ffffff;
}
#search-a{
    display: flex;
    background-color: #33397c55;
    border-radius: 25px;
}
#search-input-a{
    width: 100%;
    height: 50px;
    border: none;
    background-color: #33397c55;
    color: #ffffff;
    padding: 8px;
    outline: none;
    transition: all 0.2s ease-in-out;
}
#search-input-a:focus{
    background-color: #33397cc6;
    text-indent: 4%;
}
#search-btn-a>button{
    border-bottom-right-radius: 25px;
    height: 100%;
    background-color: #0D98BA;
    background-color: var(--hover-nav-color);
    border: none;
    font-size: 11px;
    font-weight: 600;
}
#search-btn-a>button:focus{
    box-shadow: none;
}
.home-tabs{
    margin-top: -36px;
}
.home-tabs>.nav{
    width: 100%;
    justify-content: center;
}
.home-tabs>.nav-pills{
    font-weight: bolder;
}
.home-tabs>.nav-pills>a{
    color: #0D98BA !important;
    color: var(--hover-nav-color) !important;
}
.home-tabs>.nav-pills>.nav-link.active{
    background-color: #000C66 !important;
    color: #ffffff !important;
}
#filter-div{
    margin: 10px auto;
    color: #ffffff;
}
#filter-div>div{
    background-color: #000C66;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-top-right-radius: 17px;
    height: 40px;
}
#filter-categories{
    height: 34px;
    border-top-right-radius: 17px;
    border: none;
    outline: none;
    color: #ffffff;
    font-size: 14px;
    background-color: #5149cf;
    height: 40px;
    width: 200px;
}
#tab-title{
    margin-top: 2rem;
    font-size: 30px;
    font-weight: 800;
}
@media(max-width:576px){
    #large-home-banner{
        height: 600px;
    }
    .home-tabs{
        margin-top: -68px;
        justify-content: center;
    }
}
