/* MAIN HOME PAGE */

/* FEATURED PRODUCTS */
.item-box,.learn-card{
    width: 300px;
    height: 400px;
    background-color: #ffffff;
    box-shadow: 0px 0px 4px #333322;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: column;
    padding: 1.25rem;
    border-radius: 0.25rem;
    text-align: center;
    --show-options: flex;
    transition: all 0.2s ease-in-out;
}
.item-box>p{
    font-weight: 700;
    margin-bottom: 10px;
}
.item-box:hover{
    box-shadow: 0px 1px 7px #333322;
}
.product-img-span{
    height: 260px;
    --product-option-color: #ffffff;
}
#hide-options{
    opacity: 0;
    transform: translateY(18px);
}
#show-options{
    display: var(--show-options);
    position: relative;
    bottom: 40px;
    color: var(--product-option-color);
    justify-content: space-evenly;
    opacity: 1;
    transform: translateY(0);
    transition: opacity, transform 0.2s ease-in-out;
}
.item-box>p>svg{
    color: #E8835F;
}
@media(max-width:576px){
    .item-box{
        margin:1rem auto ;
    }
}
/* FEATURED PRODUCTS */

/* LEARN */
.learn-body{
    margin-top: 2rem;
}
.learn-body>p{
    font-size: 30px;
    font-weight: 700;
    margin-left: 12px;
}
.learn-body>div{
    margin: auto;
}
.learn-card{
    height: 360px;
    color: #8b8181;
}
.learn-card:hover,.learn-card:focus{
    box-shadow: 0px 1px 7px #333322;
}
.learn-card p{
    margin-bottom: 0;
    font-weight: bolder;
}
.learn-card-title{
    margin-top: 2px;
    font-size: 14px;
    overflow: hidden;
    text-transform: uppercase;
}
.comment-badge{
    position: relative;
    bottom: 6px;
    right: 8px;
    font-size: 18px;
    font-weight: bolder;
    z-index: 200;
}
